@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Sansation:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@400;600;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-titillium-web: "Titillium Web";
  --font-outfit: Outfit;
  --font-sansation: Sansation;

  /* font sizes */
  --font-size-xl: 1.25rem;
  --font-size-base: 1rem;
  --font-size-5xl: 1.5rem;
  --font-size-17xl: 2.25rem;
  --font-size-sm: 0.88rem;
  --font-size-13xl: 2rem;

  /* Colors */
  --color-white: #fff;
  --color-gainsboro: #d9d9d9;
  --color-midnightblue-100: #25184e;
  --color-midnightblue-200: #08164a;
  --color-black: #000;
  --color-dimgray-100: #514b4b;
  --color-aliceblue: #f0f5fe;
  --color-navy: #000080;
  --color-indianred: #df5453;

  /* Gaps */
  --gap-6xs: 0.44rem;
  --gap-2xl: 1.31rem;

  /* Paddings */
  --padding-3xs: 0.63rem;

  /* border radiuses */
  --br-11xs: 2px;
}
